// Baptist church brand colors
$primary-green: #399344;
$primary-blue: #429aca;
$accent-yellow: #eac338;
$white: #ffffff;
$light-gray: #f8f9fa;
$dark-text: #333333;

// Page Header
.page-header {
  background: linear-gradient(135deg, $primary-green 0%, $primary-blue 100%);
  color: $white;
  padding: 4rem 0 3rem;
  text-align: center;
  
  h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    color: $white;
  }
  
  p {
    font-size: 1.2rem;
    opacity: 0.9;
  }
}

// Stream Section
.stream-section {
  background-color: $white;
  padding: 3rem 0;
  
  .stream-status {
    text-align: center;
    margin-bottom: 2rem;
    padding: 1rem;
    border-radius: 10px;
    
    .status-indicator {
      display: inline-block;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      margin-right: 0.5rem;
    }
    
    .status-text {
      font-size: 1.1rem;
      font-weight: 600;
      display: block;
      margin-bottom: 0.5rem;
    }
    
    .next-stream {
      font-size: 0.9rem;
      opacity: 0.8;
    }
    
    &.offline {
      background: rgba(255, 0, 0, 0.1);
      color: #d32f2f;
      
      .status-indicator {
        background: #d32f2f;
      }
    }
    
    &.live {
      background: rgba(76, 175, 80, 0.1);
      color: #388e3c;
      
      .status-indicator {
        background: #388e3c;
        animation: pulse 2s infinite;
      }
    }
  }
  
  .video-container {
    margin-bottom: 3rem;
    
    .video-placeholder {
      background: linear-gradient(135deg, rgba($primary-green, 0.1) 0%, rgba($primary-blue, 0.1) 100%);
      border-radius: 15px;
      min-height: 400px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px dashed rgba($primary-blue, 0.3);
      
      .placeholder-content {
        text-align: center;
        padding: 2rem;
        
        .play-icon {
          font-size: 4rem;
          color: $primary-green;
          margin-bottom: 1rem;
          opacity: 0.7;
        }
        
        h3 {
          color: $primary-green;
          margin-bottom: 1rem;
          font-size: 1.5rem;
        }
        
        p {
          color: #666;
          margin-bottom: 2rem;
          font-size: 1.1rem;
        }
      }
    }
  }
  
  .stream-info {
    .info-grid {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 2rem;
      max-width: 1200px;
      margin-left: auto;
      margin-right: auto;
      
      .info-card {
        flex: 0 1 300px;
        max-width: 350px;
      }
    }
    
    .info-card {
      background: $light-gray;
      padding: 2rem;
      border-radius: 15px;
      border-left: 4px solid $primary-blue;
      
      h4 {
        color: $primary-green;
        margin-bottom: 1rem;
        font-size: 1.2rem;
      }
      
      p {
        color: #666;
        line-height: 1.6;
        margin-bottom: 1rem;
      }
      
      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        
        li {
          padding: 0.5rem 0;
          color: #666;
          border-bottom: 1px solid rgba($primary-blue, 0.1);
          
          &:last-child {
            border-bottom: none;
          }
          
          &:before {
            content: "â–¶";
            color: $primary-green;
            margin-right: 0.5rem;
            font-size: 0.8rem;
          }
        }
      }
    }
  }
  
  // Countdown Timer
  .countdown-container {
    background: linear-gradient(135deg, rgba($primary-green, 0.05) 0%, rgba($primary-blue, 0.05) 100%);
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
    margin: 2rem 0;
    border: 1px solid rgba($primary-blue, 0.1);
    
    h3 {
      color: $primary-green;
      margin-bottom: 1.5rem;
      font-size: 1.3rem;
    }
    
    .countdown-timer {
      display: flex;
      justify-content: center;
      gap: 1.5rem;
      flex-wrap: wrap;
      
      .time-unit {
        background: $white;
        padding: 1rem;
        border-radius: 10px;
        min-width: 80px;
        box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
        
        .number {
          display: block;
          font-size: 2rem;
          font-weight: 700;
          color: $primary-blue;
          line-height: 1;
        }
        
        .label {
          display: block;
          font-size: 0.8rem;
          color: #666;
          margin-top: 0.25rem;
          text-transform: uppercase;
          letter-spacing: 0.5px;
        }
      }
    }
  }
  
  // Notification Section
  .notification-section {
    margin: 2rem 0;
    
    .notification-card {
      background: linear-gradient(135deg, rgba($accent-yellow, 0.1) 0%, rgba($primary-green, 0.1) 100%);
      padding: 2rem;
      border-radius: 15px;
      text-align: center;
      border: 1px solid rgba($accent-yellow, 0.2);
      
      h4 {
        color: $primary-green;
        margin-bottom: 0.5rem;
        font-size: 1.2rem;
      }
      
      p {
        color: #666;
        margin-bottom: 1.5rem;
      }
      
      .notification-options {
        display: flex;
        justify-content: center;
        gap: 1rem;
        flex-wrap: wrap;
        
        .btn-notification {
          padding: 0.8rem 1.5rem;
          border-radius: 25px;
          text-decoration: none;
          font-weight: 500;
          transition: all 0.3s ease;
          border: 2px solid transparent;
          
          &.youtube {
            background: rgba(255, 0, 0, 0.1);
            color: #ff0000;
            border-color: rgba(255, 0, 0, 0.2);
            
            &:hover {
              background: rgba(255, 0, 0, 0.2);
              transform: translateY(-2px);
              box-shadow: 0 4px 12px rgba(255, 0, 0, 0.3);
            }
          }
          
          &.discord {
            background: rgba(114, 137, 218, 0.1);
            color: #7289da;
            border-color: rgba(114, 137, 218, 0.2);
            
            &:hover {
              background: rgba(114, 137, 218, 0.2);
              transform: translateY(-2px);
              box-shadow: 0 4px 12px rgba(114, 137, 218, 0.3);
            }
          }
        }
      }
    }
  }
  
  // Discord Link in Info Card
  .info-card .discord-link {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background: rgba(114, 137, 218, 0.1);
    color: #7289da;
    text-decoration: none;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    transition: all 0.3s ease;
    
    &:hover {
      background: rgba(114, 137, 218, 0.2);
      transform: translateY(-1px);
    }
  }
}

// Live Stream Feed
.live-feed {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.05) 0%, rgba($primary-green, 0.05) 100%);
  padding: 3rem 0;
  border-top: 3px solid #ff0000;
  
  .live-stream-embed {
    max-width: 900px;
    margin: 2rem auto;
    background: $white;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    
    .embed-placeholder {
      padding: 3rem;
      text-align: center;
      background: linear-gradient(135deg, rgba(255, 0, 0, 0.1) 0%, rgba($primary-green, 0.1) 100%);
      
      p {
        color: #666;
        font-size: 1.1rem;
      }
    }
  }
}

// Past Streams
.past-streams {
  background-color: $light-gray;
  padding: 3rem 0;
  
  .streams-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin-top: 2rem;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    
    .stream-card {
      flex: 0 1 300px;
      max-width: 350px;
    }
  }
  
  .stream-card {
    background: $white;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-decoration: none;
    color: inherit;
    display: block;
    
    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
    }
    
    .stream-thumbnail {
      position: relative;
      overflow: hidden;
      
      img {
        width: 100%;
        height: 200px;
        object-fit: cover;
        transition: transform 0.3s ease;
      }
      
      .play-overlay {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: rgba(0, 0, 0, 0.7);
        color: $white;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.5rem;
        transition: all 0.3s ease;
      }
      
      .live-badge {
        position: absolute;
        top: 10px;
        left: 10px;
        background: rgba(255, 0, 0, 0.9);
        color: $white;
        padding: 0.25rem 0.5rem;
        border-radius: 5px;
        font-size: 0.7rem;
        font-weight: 600;
        animation: pulse-red 2s infinite;
      }
      
      &:hover {
        img {
          transform: scale(1.05);
        }
        
        .play-overlay {
          background: rgba($primary-green, 0.9);
          transform: translate(-50%, -50%) scale(1.1);
        }
      }
    }
    
    .stream-content {
      padding: 1.5rem;
      
      h4 {
        color: $primary-green;
        margin-bottom: 0.5rem;
        font-size: 1.1rem;
      }
      
      p {
        color: #666;
        margin-bottom: 1rem;
        font-size: 0.9rem;
      }
      
      .duration {
        background: $primary-blue;
        color: $white;
        padding: 0.25rem 0.5rem;
        border-radius: 5px;
        font-size: 0.8rem;
        font-weight: 600;
        margin-right: 0.5rem;
      }
      
      .views {
        background: rgba($accent-yellow, 0.2);
        color: #666;
        padding: 0.25rem 0.5rem;
        border-radius: 5px;
        font-size: 0.8rem;
        font-weight: 500;
      }
    }
  }
}

// Technical Info
.tech-info {
  background-color: $white;
  padding: 3rem 0;
  
  .tech-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    
    h2 {
      margin-bottom: 2rem;
      color: $primary-green;
    }
  }
  
  .tech-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin-top: 2rem;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    
    .tech-card {
      flex: 0 1 200px;
      max-width: 240px;
    }
  }
  
  .tech-card {
    background: linear-gradient(135deg, rgba($primary-green, 0.05) 0%, rgba($primary-blue, 0.05) 100%);
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
    border: 1px solid rgba($primary-blue, 0.1);
    
    h4 {
      color: $primary-green;
      margin-bottom: 1rem;
      font-size: 1.1rem;
    }
    
    p {
      color: #666;
      line-height: 1.6;
      margin: 0;
    }
  }
}

// Pulse animation for live indicator
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(56, 142, 60, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(56, 142, 60, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(56, 142, 60, 0);
  }
}

// Pulse animation for live badge
@keyframes pulse-red {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

// Responsive Design
@media (max-width: 768px) {
  .page-header {
    padding: 3rem 0 2rem;
    
    h1 {
      font-size: 2rem;
    }
    
    p {
      font-size: 1rem;
    }
  }
  
  .video-container .video-placeholder {
    min-height: 250px;
    
    .placeholder-content {
      padding: 1rem;
      
      .play-icon {
        font-size: 3rem;
      }
      
      h3 {
        font-size: 1.2rem;
      }
      
      p {
        font-size: 1rem;
      }
    }
  }
  
  .stream-info .info-grid,
  .past-streams .streams-grid,
  .tech-info .tech-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .stream-card .stream-content {
    padding: 1rem;
  }
}
